"use client";

import { LoginLink, RegisterLink } from "@kinde-oss/kinde-auth-nextjs/components";
import { Button } from "@/components/ui/button"
import { useKindeBrowserClient } from "@kinde-oss/kinde-auth-nextjs";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { useState } from "react";
import { Loader2 } from "lucide-react";

export default function Home() {
  const { isAuthenticated, user, isLoading } = useKindeBrowserClient();
  const router = useRouter();
  const [isNavigating, setIsNavigating] = useState(false);

  const handleWorkspaceNavigation = () => {
    setIsNavigating(true);
    router.push("/workspace");
  };

  if (isLoading) {
    return (
      <div className="w-full h-screen flex items-center justify-center">
        <Loader2 className="h-8 w-8 animate-spin" />
      </div>
    );
  }

  return (
    <div className="w-full h-screen flex flex-col items-center justify-center">
      <div className="max-w-7xl mx-auto">
        <div className="text-center">
          <h1 className="text-4xl md:text-5xl lg:text-6xl font-bold tracking-tight">
            <p>Your personal workspace</p>
            <p className="text-5xl md:text-6xl"> 
              for <span className="text-blue-600">better productivity</span>
            </p>
          </h1>
          <p className="mt-6 text-base lg:text-lg text-muted-foreground max-w-2xl mx-auto">
          Your project management - Simplified 
          </p>
          <div className="flex items-center justify-center gap-4 mt-6">
            {
              isAuthenticated ? <>
              <Button 
                onClick={handleWorkspaceNavigation}
                disabled={isNavigating}
              >
                {isNavigating ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    Loading workspace...
                  </>
                ) : (
                  "Go to workspace"
                )}
              </Button>
              </> : <>
              <Button>
                <RegisterLink>
                  Get Started
                </RegisterLink>
              </Button>
              <Button asChild variant={"outline"}>
                <LoginLink>
                  Sign in
                </LoginLink>
              </Button>
              </>
            }
          </div>
        </div>
      </div>
    </div>
  );
}
